* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
}

body {
  /* margin: 0;
  padding: 0; */
  min-width: 320px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App__header {
    background-color: #e91d61;
    text-align: center;
    margin-top: 1rem;
    padding: .75rem .25rem;
}

.App__title {
    font-size: 1.75rem;
    color: #fff;
}

.App__nav {
    margin-top: .5rem;
}

.menu-list__item {
    margin-bottom: .25rem;
}

.menu-list__link {
    display: block;
    text-decoration: none;
    color: #fff;
    font-weight: 700;
}

.menu-list__link.active {
    color: #ff0;
}

@media (orientation: landscape) {
    .App__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1rem;
    }

    .menu-list {
        display: flex;
    }

    .menu-list__item {
        margin: 0 .5rem;
    }
}

@media (min-width: 1024px) {
    .App__header {
        margin-top: 1.5rem;
        padding: 1.5rem;
    }

    .App__title {
        font-size: 2.5rem;
    }

    .menu-list__item {
        margin: 0 1rem;
    }

    .menu-list__link {
        font-size: 1.5rem;
    }

    .menu-list__link:hover {
        color: #ff0;
    }
}

@media (min-width: 1600px) {
    .App__header {
        padding: 2rem;
    }

    .App__title {
        font-size: 3.5rem;
    }

    .menu-list__item {
        margin: 0 1.5rem;
    }

    .menu-list__link {
        font-size: 2rem;
    }
}
.search-form {
    font-size: 0;
    text-align: center;
    margin: 1rem 0;
}

.search-form__input,
.search-form__button {
    padding: .25rem .5rem;
    border: 1px solid #14bdac;
    outline: none;
}

.search-form__input {
    border-top-left-radius: .25rem;
    border-bottom-left-radius: .25rem;
}

.search-form__button {
    padding-left: .25rem;
    padding-right: .25rem;
    color: #fff;
    background-color: #14bdac;
    border-top-right-radius: .25rem;
    border-bottom-right-radius: .25rem;
    cursor: pointer;
}

@media (min-width: 768px) {
    .search-form {
        margin: 2rem 0 1rem;
    }

    .search-form__input,
    .search-form__button {
        font-size: 1rem;
        padding: .5rem;
    }

    .search-form__input {
        text-indent: .5rem;
        width: 50%;
        max-width: 600px;
    }
}

@media (min-width: 1200px) {

    .search-form__input,
    .search-form__button {
        font-size: 1.5rem;
        border-width: 2px;
        padding: .75rem .5rem;
    }

    .search-form__button {
        font-weight: 700;
        letter-spacing: 1px;
    }
}

@media (min-width: 1600px) {
    .search-form {
        margin: 3rem 0 1.5rem;
    }
}
.container {
    margin: 1.5rem 1rem;
}

.card {
    margin-bottom: 1.5rem;
    padding: 1rem;
    border: 3px solid #14bdac;
    border-radius: .5rem;
    background-color: #e9e9e9;
    position: relative;
}

.card__img {
    height: 20vmax;
    max-height: 250px;
}

.card__title {
    line-height: 1.15;
    margin: .5rem 0;
}

.card__text--gray {
    color: #353535;
    margin-bottom: .5rem;
}

.fa-star {
    position: absolute;
    top: 5%;
    right: 15%;
    font-size: 2rem;
    color: goldenrod;
    cursor: pointer;
}

.info {
    font-size: 1.5rem;
    font-weight: 700;
    letter-spacing: 1px;
    text-align: center;
    padding: 3rem 0;
}

@media (min-width: 411px) {
    .container {
        margin: 1.5rem 2rem;
    }

    .card {
        display: flex;
    }

    .card__col {
        margin-right: 1rem;
        flex: 1 0 25%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .card__text--sm {
        text-align: center;
        margin-top: .5rem;
    }

    .fa-star {
        font-size: 1rem;
        top: 3%;
        right: 3%;
    }
}

@media (min-width: 768px) {
    .container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: 0;
    }

    .card {
        flex-basis: 48%;
        max-width: 575px;
    }

    .info {
        flex-basis: 100%;
        font-size: 2rem;
    }
}

@media (min-width: 1358px) {
    .container {
        justify-content: space-around;
        margin: 3rem 2rem;
    }

    .card {
        padding-right: 2rem;
    }

    .fa-star {
        font-size: 2rem;
        top: 5%;
    }
}
